.page-content {
  width: 100%;
  height: 100vh;
}

#listOfDocs {
  background-color: #009688;
  padding-left: 0px;
  margin: 0;
  width: 208px;
  height: 100%;
  list-style: none;

  li {
    height: 50px;
    color: #f0f0f0;
    line-height: 50px;
    padding-left: 8px;
    transition: background-color 0.2s ease-in-out;

    &:nth-child(even) {
      background-color: #26A69A;
    }
    &:nth-child(odd) {
      background-color: #4DB6AC;
    }
    &:hover {
      background-color: #009688;
      color: #ffffff;
    }
  }
}

#HomeSection {
  height: 100%;
}

#docContainer {
  position: absolute;
  top: 0;
  margin-left: 214px;
  width: calc(100% - 214px);
}

#input {
  width: 100%;
  min-height: 100vh;
  border: none;
}

.marked {
  code {
    background-color: #2c2c2c;
    color: #66b8da;
    padding: 4px 4px;
  }

  pre {

    code {
      background-color: white;
      border: solid 1px darkgray;
      border-radius: 2px;
      padding: 5px;
      color: #1a2c51;
    }
  }
}

.mdl-button--fab {
  position: fixed !important;
  bottom: 1vh;
}

#removeButton {
  right: calc(2vh + 10px);
}

#editButton {
  right: calc(2vh + 75px);
}

#ToolSave {
  text-align: center;
}

.off {
  pointer-events: none;
}